@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

@media only screen and (max-width: 400px) {
  header {
    height: 100px !important;
  }

  main {
    padding-top: 100px !important;
    /* header 50px height*/
  }

  .outlinediv {
    max-width: 350px;
  }
}

/* h2 is About */
h2 {
  text-align: left;
  font-family: Verdana, Tahoma;
  letter-spacing: 1.5px;
  border-bottom: 4px solid #dfdfe2;
  max-width: 80%;
  margin-top: 0px;
  padding-top: 10px;
}

h2::before {
  content: "//";
}

.info {
  padding: 0.5px;
}

/* About */

main {
  padding-top: 50px;
  /* header 50px height*/
}

body {
  text-align: center;
  align-items: center;
  background: #1b1b32;
  color: #efefef;
  font-family: Helvetica;
  margin: 0;
}

body a {
  color: #efefef;
  text-decoration: none !important;
}

body a:hover {
  text-decoration: underline;
  background-color: #f1be32;
  color: #000000;
}

.outlinediv {
  max-width: 1050px;
  width: 85%;
  text-align: justify;
  padding: 2% 0% 2% 0%;
  margin-left: auto;
  margin-right: auto;
  background-color: #888888;
  color: #000000;
  border-radius: 15px;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 10px;
}

.pad {
  width: 100%;
  display: flex;
  flex-flow: row;
  justify-content: stretch;
}

.keypad {
  order: 1;
  width: 70%;
  text-align: justify;
  padding: 0%;
  color: #000000;
  border-radius: 15px;
  /* flex align*/
  justify-content: center;
  align-items: center;
  align-content: stretch;
  /* Use CSS Grid to create a 3x3 grid ;*/
  display: grid;
  grid-template-columns: repeat(3, 30%);
  gap: 5px;
}

.fnpad {
  width: 40%;
  /*flex box order of .pad*/
  order: 2;

  text-align: justify;
  padding: 0%;
  /*background-color: #ff0000;*/
  color: #000000;
  border-radius: 15px;

  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  gap: 10px;
}

.delpad {
  width: 100%;
  text-align: justify;
  padding: 0%;
  /*background-color: #0091ff;*/
  color: #000000;
  border-radius: 15px;
  /* flex align*/
  justify-content: center;
  align-items: center;
  align-content: stretch;
  /* Use CSS Grid to create a 2x1 grid */
  display: grid;
  grid-template-columns: repeat(2, 40%);
  gap: 5px;
  /* Adjust the gap between drum pads as needed */
}

.opspad {
  width: 100%;
  text-align: justify;
  padding: 0%;
  /*   background-color: #f1be32;*/
  color: #000000;
  border-radius: 15px;
  /* flex align*/
  justify-content: center;
  align-items: center;
  align-content: stretch;
  /* Use CSS Grid to create a 2x2 grid */
  display: grid;
  grid-template-columns: repeat(2, 40%);
  gap: 5px;
}

.keypad .numkey,
.keypad .numkeyzero,
.opspad .opskey,
.opspad .opskeyequals,
.opspad .opskeyans,
.delpad .delkey {
  cursor: pointer;
  font-family: Verdana, Tahoma;
  font-weight: bold;
  /* Use flexbox to center content and make {id} center */
  display: flex;
  align-items: center;
  justify-content: center;

  /* https://stackoverflow.com/questions/30693928/how-to-make-font-size-relative-to-parent-div */
  container-type: size;
}

.keypad .numkey,
.keypad .numkeyzero {
  background-color: #356e00;
  border: 5px outset #000000;
  border-radius: 15px;
}

.opspad .opskey,
.opspad .opskeyequals,
.opspad .opskeyans {
  border: 5px outset #000000;
  border-radius: 15px;
}

.opspad .opskey {
  background-color: #636363;
}

.opspad .opskeyequals {
  background-color: #a87000;
  color: #1b1b32;
  aspect-ratio: 2/1;
}

.opspad .opskeyans {
  background-color: #1b1b32;
  color: #888888;
  aspect-ratio: 3/1;
}

.delpad .delkey {
  background-color: #98335f;
  border: 5px outset #000000;
  border-radius: 15px;
}

.keypad .numkey,
.opspad .opskey,
.delpad .delkey {
  aspect-ratio: 1/1;
}

.keypad .numkeyzero {
  aspect-ratio: 2/1;
}

.keypad .numkey:hover,
.keypad .numkeyzero:hover,
.opspad .opskey:hover,
.opspad .opskeyequals:hover,
.opspad .opskeyans:hover,
.delpad .delkey:hover {
  background-image: linear-gradient(rgba(192, 192, 192, 0.4) 0 0);
}

.keypad .numkey:active,
.keypad .numkeyzero:active,
.opspad .opskey:active,
.opspad .opskeyequals:active,
.opspad .opskeyans:active,
.delpad .delkey:active {
  background-image: linear-gradient(rgba(192, 192, 192, 0.6) 0 0);
  transform: translate(4px, 4px);
}

.keypad .numkey span,
.keypad .numkeyzero span,
.opspad .opskeyans span,
.delpad .delkey span {
  resize: both;
  font-size: 50cqmin;
}

.opspad .opskey span {
  resize: both;
  font-size: 70cqmin;
  font-weight: bold;
}

.opspad .opskeyequals span {
  resize: both;
  font-size: 60cqmin;
  font-weight: bold;
}

#outerdisplay {
  /*height: 300px;*/
  width: 100%;
  margin: auto;

  container-type: inline-size;
  /*use with child, font-size: 30cqh;*/

  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  gap: 10px;

}

.display {
  width: 88%;
  height: 20%;
  margin: auto;
  padding: 2%;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  display: flex;
}

#display {
  /*result display*/
  color: #1b1b32;
  background-color: #9bbc0f;
  border: 10px outset #000000;
  border-radius: 10px;
  font-size: 15cqmin;
  overflow: hidden;
  resize: none;
}

#display.align-right {
  text-align: right;
  justify-content: right;
  align-items: right;
}

#display.align-left {
  text-align: left;
  justify-content: left;
  align-items: left;
}

#inputdisplay {
  max-height: 2.4em;
  /* (Number of lines you want visible) * (line-height) */
  line-height: 1.2em;
  color: #d1d1d1;
  background-color: #272929;
  border: 7px solid #272929;
  border-radius: 5px;
  font-size: 7.6cqmin;
  text-align: right;
  justify-content: right;
  align-items: right;
  resize: vertical;
  /*scroll to end*/
  overflow-y: auto;
  overflow-wrap: anywhere;
}